<template>
  <div>
    <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
      <!-- 电站组织树 -->

      <CommonTree
        title="电站组织"
        @select="handleSelect"
        :value="treeData"
        class="left-tree-layout"
        :toolbar="true"
        :canEdit="false"
        :canAdd="false"
        :canDelete="false"
        :isShowOperationBtns="false"
        :fieldNames="{ key: 'id', title: 'name' }"
      />

      <!-- 数据表格 -->
      <div class="right-table-layout">
        <BasicTable
          class="fix-basic-table"
          @register="registerTable"
          :searchInfo="searchInfo"
          @resize-column="(w, col) => (col.width = w)"
          @row-db-click="(record) => handleView(record)"
        >
          <template #toolbar>
            <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">添加</a-button>
            <!-- <a-button type="primary" @click="handleUpdateIndex">修改</a-button> -->
            <a-button
              class="red-btn"
              :disabled="selectedRowId == ''"
              :preIcon="IconEnum.DELETE"
              @click="handleDelete"
              >删除</a-button
            >
            <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExportIndex"
              >导出</a-button
            >
          </template>

          <template #bodyCell="{ column, text, record }">
            <template v-if="column.key === 'action'">
              <TableAction
                :actions="[
                  {
                    icon: IconEnum.LOG,
                    label: '进度',
                    onClick: handleView.bind(null, record),
                  },
                  {
                    icon: IconEnum.DELETE,
                    danger: true,
                    label: '取消',
                    ifShow: () => {
                      return record.status == 0;
                    },
                    onClick: handleCancel.bind(null, record),
                  },
                ]"
              />
            </template>
          </template>
        </BasicTable>
      </div>
    </PageWrapper>
  </div>
  <!-- <settlemenElecModal @reload="handleReload" @register="registerDrawer" /> -->
</template>
<script lang="ts" setup>
  import type { FormProps } from '/@/components/Form/src/types/form';
  import { onMounted, reactive, ref, toRaw } from 'vue';
  import { BasicTable, useTable, TableAction, SorterResult } from '/@/components/Table';
  import { TreeItem } from '/@/components/Tree';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { message, SelectProps } from 'ant-design-vue';
  import { jsonToSheetXlsx } from '/@/components/Excel';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import {
    getSettlementElectricityList,
    getTreeData,
    formState,
    getIndex,
    deleteSettlementElectricity,
    getSettlementElectricityListColumns,
    exportExcel,
  } from './settlementElectricity';
  import { assign } from 'lodash-es';
  import { useRouter } from 'vue-router';
  import { ElMessageBox } from 'element-plus';
  import { useI18n } from '@/hooks/web/useI18n';
  import { cancelProcessInstance } from '@/api/bpm/processInstance';
  import { IconEnum } from '@/enums/appEnum';
  import { addTabPage } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import { useMessage } from '@/hooks/web/useMessage';
  import * as CommonUtil from '/@/utils/common';
  import { useUserStore } from '/@/store/modules/user';

  const { createConfirm } = useMessage();
  const { t } = useI18n();
  const searchInfo = reactive<any>({}); // 查询信息
  const treeData = ref<TreeItem[]>([]); // 左侧电站树数据
  const selectedNode = ref({}); // 被选择的电站节点
  const periodParam = ref({}); // 查询分期下拉框数据的参数

  const checkedKeys = ref<Array<string | number>>([]); // 复选框的选择的数据
  const options = ref<SelectProps['options']>([]); //电站分期
  const areaColumn = getSettlementElectricityListColumns(options);
  const router = useRouter();

  const indexList = ref<Array<any>>([]);
  const getSettlementElectricityListByParam = async () => {
    let areaList = await getSettlementElectricityList(selectedNode.value);
    indexList.value = areaList.result;
    return areaList.result;
  };
  const searchForm: FormProps = {
    showAdvancedButton: false,
    baseColProps: { span: 8 },
    labelWidth: 120,
    autoAdvancedLine: 1,
    schemas: [
      { field: 'billCode', component: 'Input', label: '单据编号' },
      {
        field: '[startDate, endDate]',
        label: ' 结算周期起止',
        component: 'RangePicker',
        componentProps: {
          format: 'YYYY-MM-DD',
          valueFormat: 'YYYY-MM-DD',
          placeholder: ['开始日期', '结束日期'],
        },
      },
      {
        field: 'status',
        label: '审批状态',
        component: 'DictSelectBox',
        componentProps: {
          type: 'bpm_process_instance_result',
        },
      },
    ],
  };

  // 注册Drawer
  // const [registerDrawer, { openDrawer: opensettlemenElecModal }] = useDrawer();

  const [registerTable, { reload, getPaginationRef }] = useTable({
    title: '结算电量列表',
    api: async () => {
      const paginationRef = getPaginationRef() as any;
      selectedNode.value.pageNo = paginationRef.current;
      selectedNode.value.pageSize = paginationRef.pageSize;
      let dataObj = await getSettlementElectricityListByParam();
      return dataObj;
    },
    columns: areaColumn,
    useSearchForm: true,
    formConfig: searchForm,
    showTableSetting: true,
    bordered: true,
    // eslint-disable-next-line no-dupe-keys
    rowKey: 'id',
    // actionColumn: {
    //   width: 160,
    //   title: '操作',
    //   dataIndex: 'action',
    // },
    pagination: {
      defaultPageSize: 20,
      pageSize: 20,
      showSizeChanger: true,
      showQuickJumper: true,
    },
    rowSelection: {
      type: 'radio',
      onChange: onSelect,
    },
    handleSearchInfoFn(info) {
      if (info.startDate != undefined) {
        selectedNode.value.startDate = info.startDate + ' 00:00:00';
      } else {
        selectedNode.value.startDate = null;
      }
      if (info.endDate != undefined) {
        selectedNode.value.endDate = info.endDate + ' 00:00:00';
      } else {
        selectedNode.value.endDate = null;
      }
      selectedNode.value.status = info.status;
      selectedNode.value.billCode = info.billCode;
      return info;
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        selectedNode.value.sortField = sortInfo.field;
        selectedNode.value.sortOrder = sortInfo.order.replace('end', '');
      } else {
        selectedNode.value.sortField = null;
        selectedNode.value.sortOrder = null;
      }
    },
  });
  let selectedRowId = ref('');
  function onSelect(record, selected) {
    if (selected.length > 0) {
      checkedKeys.value = [record[0]];
      selectedRowId.value = record[0];
    } else {
      checkedKeys.value = [];
      selectedRowId.value = '';
    }
  }
  function onSelectAll(selected, selectedRows, changeRows) {
    const changeIds = changeRows.map((item) => item.id);
    if (selected) {
      checkedKeys.value = [...checkedKeys.value, ...changeIds];
    } else {
      checkedKeys.value = checkedKeys.value.filter((id) => {
        return !changeIds.includes(id);
      });
    }
  }

  // 查询左侧电站树数据
  async function queryDeptTreeList() {
    const deptList = (await getTreeData({})) as unknown as TreeItem[];
    treeData.value = deptList.result;
    // let org = deptList.result[0];
    // selectedNode.value.nodeKindId = org.nodeKindId;
    // selectedNode.value.searchId = org.id;
    reload();
  }
  // 添加区域
  function handleCreate() {
    if (selectedNode.value.periodId == null) {
      message.error('请先选择电站分期。');
      return;
    }
    if (selectedNode.value.nodeKindId == 'station') {
      getOptions();
    }
    if (selectedNode.value.nodeKindId == 'period') {
      getOptions();
    }

    // opensettlemenElecModal(true);
    // router.push(`/po/elec/settlementelec/setElecCreate`);
    addTabPage(`/po/elec/settlementelec/setElecCreate`, '添加结算电量', toRaw(formState));
  }
  function handleView(record: any) {
    let str = '';
    if (record.status == 0) {
      str = '修改';
    } else if (record.status == 1) {
      str = '审批';
    } else {
      str = '查看';
    }
    CommonUtil.toFlowPage(record.id);
    // addTabPage(`/po/elec/settlementelec/setElecDetail?id=${record.id}`, str + '结算电量');
  }

  const handleCancel = async (record: any) => {
    const res = await getIndex({ id: record.id });
    const { value } = await ElMessageBox.prompt('请输入取消原因', '取消流程', {
      confirmButtonText: t('common.okText'),
      cancelButtonText: t('common.cancelText'),
      inputPattern: /^[\s\S]*.*\S[\s\S]*$/,
      inputErrorMessage: '取消原因不能为空',
    });
    // 发起取消
    await cancelProcessInstance(res.result.processInstanceId, value);
    message.success('已取消');
    reload();
  };

  // 删除区域
  async function handleDelete() {
    if (checkedKeys.value.length != 1) {
      message.info('请选择一条数据.');
      return false;
    }
    const res = await getIndex({ id: checkedKeys.value[0] });

    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        if (res.result.status != 0) {
          message.error('非草稿状态不能删除。');
          return;
        }
        await ProcessInstanceApi.deleteProcessInstance(res.result.processInstanceId);
        message.success('已删除。');
        checkedKeys.value = [];
        reload();
      },
    });

    // deleteSettlementElectricity(checkedKeys.value[0]).then(() => {
    //   message.success('已删除。');
    //   checkedKeys.value = [];
    //   reload();
    // });
  }

  // 根据电站id获取分期信息
  function getOptions() {
    let param = { stationId: periodParam.value.stationId };
    // let param = { stationId: '0E012356294F4AC2B81315E1F81BEBB9' };
  }
  // 左侧树状菜单选中事件
  function handleSelect(node = {}) {
    selectedNode.value.stationId = null;
    selectedNode.value.stationName = null;
    selectedNode.value.periodName = null;
    selectedNode.value.periodId = null;
    selectedNode.value.nodeKindId = null;
    periodParam.value.nodeKindId = null;
    selectedNode.value.searchId = null;
    selectedNode.value.stationOrganId = null;
    selectedNode.value.stationOrganName = null;
    if (node != null) {
      selectedNode.value.stationOrganId = node.organId;
      selectedNode.value.stationOrganName = node.organName;
      if (node.nodeKindId == 'station') {
        selectedNode.value.stationId = node.powerStationId;
        selectedNode.value.stationName = node.name;
        periodParam.value.stationId = node.powerStationId;
      }
      if (node.nodeKindId == 'period') {
        selectedNode.value.stationId = node.powerStationId;
        selectedNode.value.stationName = node.orgName;
        selectedNode.value.periodName = node.powerStationPeriodName;
        selectedNode.value.periodId = node.powerStationPeriodId;
        periodParam.value.stationId = node.powerStationId;

        formState.stationId = selectedNode.value.stationId;
        formState.stationName = selectedNode.value.stationName;
        formState.periodName = selectedNode.value.periodName;
        formState.periodId = selectedNode.value.periodId;
        formState.stationOrganId = selectedNode.value.stationOrganId;
        formState.stationOrganName = selectedNode.value.stationOrganName;
      }
      selectedNode.value.nodeKindId = node.nodeKindId;
      periodParam.value.nodeKindId = node.nodeKindId;
      selectedNode.value.searchId = node.id;
    }
    reload();
  }

  function checkSelectData() {
    if (checkedKeys.value.length == 0 || checkedKeys.value.length > 1 || !checkedKeys.value['0']) {
      message.error('请选择一行数据');
      return false;
    }
    return true;
  }

  // 导出
  async function handleExportIndex() {
    createConfirm({
      title: '导出',
      iconType: 'warning',
      content: '确定要导出所有记录？',
      async onOk() {
        const header = ref({ index: '序号' });

        const indexColumns = getSettlementElectricityListColumns();
        const data = ref<Array<any>>([]);
        const keys = ref<Array<any>>([]);
        indexColumns.forEach((column) => {
          if (!(column.dataIndex === 'id')) {
            if (column.dataIndex === 'stationOrganName' && useUserStore().isMultiOrganization) {
              header.value[column.dataIndex] = column.title;
              keys.value.push(column.dataIndex);
            } else if (!(column.dataIndex === 'stationOrganName')) {
              header.value[column.dataIndex] = column.title;
              keys.value.push(column.dataIndex);
            }
          }
        });
        let areaList = await exportExcel(selectedNode.value);
        indexList.value = areaList.result;

        indexList.value.forEach(function (item, index) {
          let statusDisplayName = '';
          switch (item.status) {
            case 0:
              statusDisplayName = '草稿';
              break;
            case 1:
              statusDisplayName = '处理中';
              break;
            case 2:
              statusDisplayName = '通过';
              break;
            case 3:
              statusDisplayName = '不通过';
              break;
            case 4:
              statusDisplayName = '已取消';
              break;
            case 5:
              statusDisplayName = '已终止';
              break;
            case 6:
              statusDisplayName = '退回/驳回';
              break;
          }
          let exportItem = ref({ index: index + 1 });
          keys.value.forEach((key) => {
            exportItem.value[key] = key === 'status' ? statusDisplayName : item[key];
          });
          data.value.push(exportItem.value);
        });
        jsonToSheetXlsx({
          data: data.value,
          header: header.value,
          filename: '结算电量.xlsx',
        });
      },
    });
  }

  // 启动执行代码
  onMounted(() => {
    queryDeptTreeList();

    MsgManager.getInstance().listen('settlementElectricity', function () {
      setTimeout(() => {
        reload();
      }, 500);
    });
  });
</script>
<style lang="less" scoped>
  .dialog-loading {
    position: absolute;
    z-index: 1000 !important;
    top: 45%;
    left: 45%;
  }

  .dialog-mask {
    position: absolute;
    z-index: 1001 !important;
    top: -1px;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    background: #ccc;
  }

  .left-tree-layout {
    flex-shrink: 0;
    width: 298px;
    border-radius: 2px;
  }

  .right-table-layout {
    width: calc(100% - 308px);
  }

  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings) {
      margin-right: 0;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
